import styled from 'styled-components';

export const Box = styled.div`
  color: #fff;
  margin-bottom: 1rem;
  background-color: #007fff;
  border-radius: 8px;
  padding: 0.2rem 1rem;
  font-size: 1rem;
  height: 100%;
  text-align: center;
  &:empty {
    padding: 1rem;
  }
`;



